        this.imgIndex++
        if (this.imgIndex >= this.imgCon.length) {
          this.imgIndex = 0
        }
      }, 3000)
    },
    _stop () {
      // 
      clearInterval(this.carouselTimer)
    },
    pointClick (i) {
      this._stop()
      this.imgIndex = i
      console.log(this.carouselTimer)
    }
